<template>
  <leftSideBar :order="this.order" :total="this.total" @mobileChange="mobileEmmited"/>
  <mainMenuComponent @onOrder="emitted" :mobile="this.mobile" />
</template>

<script>
import leftSideBar from './components/sidebar-component';
import mainMenuComponent from './components/menu-component';
export default {
  name: 'App',
  data(){
    return{
      order:[],
      total:0,
      mobile:""
    }
  },
  components: {
    leftSideBar,
    mainMenuComponent,
  },
  methods:{
    emitted(fromChild,t){
      this.order=fromChild;
      this.total=t;
    },
    mobileEmmited(mobile){
      this.mobile = mobile
    }
  }
}
</script>

