<template>
    <!-- <div id="h3Container">
        <h3>Here's what your tummy might Need !</h3>
    </div> -->
    <div id="btnContainer">
        <button class="btn" :class="active[0] ? 'active' : pass" style="font-size:1.25vw"
            @click="filterAll(), this.active[0] = 1, changeState(0)">All</button>
        <button class="btn" :class="active[1] ? 'active' : pass" style="font-size:1.25vw"
            @click="filterSuggestions(), this.active[1] = 1, changeState(1)">Recommended</button>
        <button class="btn" :class="active[2] ? 'active' : pass" style="font-size:1.25vw"
            @click="filterInCart(), this.active[2] = 1, changeState(2)">Already in Cart</button>
        <button class="btn" :class="active[3] ? 'active' : pass" style="font-size:1.25vw"
            @click="filterStarters(), this.active[3] = 1, changeState(3)">Starters</button>
        <button class="btn" :class="active[4] ? 'active' : pass" style="font-size:1.25vw"
            @click="filterMain(), this.active[4] = 1, changeState(4)">Main Course</button>
        <button class="btn" :class="active[5] ? 'active' : pass" style="font-size:1.25vw"
            @click="filterBeve(), this.active[5] = 1, changeState(5)">Beverages</button>
        <button class="btn" :class="active[6] ? 'active' : pass" style="font-size:1.25vw"
            @click="filterDesert(), this.active[6] = 1, changeState(6)">Deserts</button>
        <button class="btn" :class="active[7] ? 'active' : pass" style="font-size:1.25vw"
            @click="filterSpecail(), this.active[7] = 1, changeState(7)">Specials<span class="special"
                style="margin: 0vw 0vw 0vw 0.6vw">
                <img src="../assets/premium-quality.png" alt="">
            </span></button>
        <button class="btn" :class="active[8] ? 'active' : pass"
            @click="filterSpicy(), this.active[8] = 1, changeState(8)">Spicy <img class="chilly" src="../assets/spicy.png"
                alt="" style="width: 2.2vw;margin: 0vw 0vw 0vw 0.6vw;"> </button>
    </div>
    <div v-if="show_not_found_msg == true">
        <label class="warning">No previous orders found</label>
    </div>
    <div v-if="show_no_mob_num == true">
        <label class="warning">No mobile number entered</label>
    </div>
    <div>
        <input class="input" type="text" placeholder="Search your food here" v-model="search">
    </div>
    <div id="menuContainer">
        <div class="card" v-for="item, index in menuItems" :key="item.type">
            <div class="card-body">
                <span>
                    <h4 class="card-title ">{{ item.itemName }} <img class="chilly" src="../assets/spicy.png"
                            alt="(spicy)" v-if="item.isSpicy">
                        <span v-if="item.isSpecial" class="special"><img src="../assets/premium-quality.png"
                                alt=""></span>
                    </h4>

                </span>
                <h5 class="itemprice">{{ item.itemPrice }}₹</h5>
                <h6 class="itemtype">{{ item.itemType }}</h6>

                <div>
                    <label class="ingredients" v-for="i in item.ingredients" :key="i">{{ i }},</label>
                </div>
                <button class="btn" v-if="item.itemQuan == 0"
                    @click="item.itemQuan += 1, addToOrder(item.itemName, item.itemPrice, item.itemQuan), this.total += item.itemPrice, sendToParent(), item.isInCart = !item.isInCart">
                    ADD</button>

                <div v-if="item.itemQuan > 0">
                    <button class="btn-red"
                        @click="item.itemQuan -= 1, addToOrder(item.itemName, item.itemPrice, item.itemQuan, index), this.total -= item.itemPrice, sendToParent()"><span>-</span></button>
                    <span>{{ item.itemQuan }}</span>
                    <button class="btn-green"
                        @click="item.itemQuan += 1, addToOrder(item.itemName, item.itemPrice, item.itemQuan, index), this.total += item.itemPrice, sendToParent()"><span>+</span></button>
                </div>
                <div v-if="item.isSuggested == true">
                    <button class="btn-red" @click="not_interested(index)">Not interested</button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'mainMenuComponent',
    emits: ["onOrder"],
    props: ['mobile'],
    data() {
        return {
            menuItems: [],
            allMenuItems: [],
            order: [],
            total: 0,
            search: '',
            sugg: [],
            show_no_mob_num: false,
            show_not_found_msg: false,
            active: [1, 0, 0, 0, 0, 0, 0, 0, 0],
        }
    },
    watch: {
        search() {
            this.search_item();
        }
    },
    methods: {
        addToOrder(n, p, q, i) {
            var pos = this.order.map(e => e.name).indexOf(n);
            if (pos != -1) {
                this.order[pos].quan = q;
                this.order[pos].ePrice = p * q;
                if (this.order[pos].quan == 0) {
                    this.menuItems[i].isInCart = false;
                    //this.total-=temp;
                    this.order.splice(pos, 1);
                }
            }
            else {
                this.order.push({ name: n, price: p, quan: q, ePrice: p * q })
                //var pos2=this.order.map(e=>e.name).indexOf(n);
            }
        },
        sendToParent() {
            this.$emit('onOrder', this.order, this.total);
        },
        filterStarters() {
            this.menuItems = this.allMenuItems.filter(function (item) {
                return item.itemType === 'Starter';
            });
        },
        filterAll() {
            this.menuItems = this.allMenuItems;
        },
        filterMain() {
            this.menuItems = this.allMenuItems.filter(function (item) {
                return item.itemType === 'Main course';
            });
        },
        filterBeve() {
            this.menuItems = this.allMenuItems.filter(function (item) {
                return item.itemType === 'Beverage';
            });
        },
        filterDesert() {
            this.menuItems = this.allMenuItems.filter(function (item) {
                return item.itemType === 'Desert';
            });
        },
        filterSpecail() {
            this.menuItems = this.allMenuItems.filter(function (item) {
                return item.isSpecial === true;
            });
        },
        filterSpicy() {
            this.menuItems = this.allMenuItems.filter(function (item) {
                return item.isSpicy === true;
            });
        },
        filterInCart() {
            this.menuItems = this.allMenuItems.filter(function (item) {
                return item.isInCart === true;
            });
        },
        search_item() {
            if (this.search.trim() == "") {
                this.menuItems = this.allMenuItems
            }
            this.menuItems = this.allMenuItems.filter(item => {
                if (item.itemName.toLowerCase().indexOf(this.search.toLowerCase()) != -1) {
                    return true
                }
            })
        },
        filterSuggestions() {
            if (this.mobile.length < 10) {
                this.show_no_mob_num = true;
            }
            if (this.mobile.length == 10) {
                this.show_no_mob_num = false;
                fetch('http://localhost/orders/findUser' + this.mobile)
                    .then((response) => {

                        if (response.ok) {
                            return response.json();
                        }
                        else {
                            console.log("error");
                        }
                    }).then((data) => {
                        if (data.length == 0) {
                            this.show_not_found_msg = true;
                        } else {
                            this.sugg = data[0].ingredients;
                            this.show_not_found_msg = false;
                        }
                    }
                    )
                setTimeout(() => {
                    this.menuItems = [];
                    this.sugg.forEach(i => {
                        this.allMenuItems.forEach(item => {
                            if (item.ingredients.indexOf(i) > -1) {
                                if (this.menuItems.indexOf(item) > -1) {
                                    //
                                } else {
                                    item.isSuggested = true;
                                    this.menuItems.push(item);
                                }
                            }
                        });
                    });
                }, 1000)
            }
        },
        not_interested(index) {
            console.log(this.sugg)
            this.sugg.forEach(s => {
                if (this.menuItems[index].ingredients.indexOf(s) > -1) {

                    this.sugg.splice(this.sugg.indexOf(s), 1);
                }
            });
            this.menuItems.splice(index, 1);
            fetch('http://localhost/orders/updateSuggestion' + this.mobile, {
                method: 'PATCH',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    ingredients: this.sugg
                })
            });
        },
        changeState(i) {
            for (let index = 0; index < this.active.length; index++) {
                if (index == i) {
                    //Notthing
                }
                else {
                    this.active[index] = 0
                }

            }
        }
    },
    created() {
        fetch('http://localhost/getFoodItems')
            .then((response) => {
                if (response.ok) {
                    return response.json();
                }
                else {
                    console.log("error");
                }
            }).then((data) => {
                const results = [];
                for (const id in data) {
                    results.push({
                        id: data[id]._id,
                        isSpecial: data[id].isSpecial,
                        isSpicy: data[id].isSpicy,
                        itemName: data[id].itemName,
                        itemPrice: data[id].itemPrice,
                        itemType: data[id].itemType,
                        ingredients: data[id].ingredients,
                        itemQuan: 0,
                        isInCart: false,
                        isSuggested: false
                    })
                }
                this.menuItems = results;
                this.allMenuItems = this.menuItems;
            })
    }
}
</script>
<style scoped>
/* For Menu heading */
@import url('https://fonts.googleapis.com/css2?family=Coda&display=swap');
/* For all elements */
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@500&display=swap');
/* For item names */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

* {
    font-family: 'Nunito', sans-serif;
}

#h3Container>h3 {
    /* DONT TOUCH */
    font-family: 'Bad Script', cursive;
    font-weight: 900;
    font-size: 3vw;
    letter-spacing: 0.4vw;
}

#menuContainer {
    display: flex;
    /* position: absolute;
    top: 0px; */
    width: 70%;
    flex-wrap: wrap;
    gap: 20px;
    overflow: hidden;
}

#btnContainer {
    width: 70%;
    display: flex;
    flex-wrap: wrap;
    margin: 0px;
    background: rgba(0, 0, 0, 0.781);
    padding: 0.5vw;
    border-bottom: black solid 2px;
}

#btnContainer>* {
    margin: 0.5vw 0.5vw 0.5vw 0.5vw;
}

h3 {
    margin-bottom: 0.5%;
}

.card {
    height: 250px;
    flex: 1 0 200px;
    max-width: 40vh;
    height: 120%;
    /* max-height: 40vh; */
    border-radius: 5%;
    margin: 0.4vw;
    /* DONT TOUCH */
    transition: all 0.2s;
    border: 2px solid rgb(0, 0, 0);
    background: rgba(0, 0, 0, 0.877);
    z-index: 1;

}

.card:hover {
    transform: scale(1.04);
    border: 2px solid rgba(245, 143, 96, 0.842);
    transition: all 0.2s;
    background: rgba(0, 0, 0, 0.952);
    animation: card-hover 1s  infinite alternate linear;
    /* animation: shake 1s 4s infinite alternate linear; */
}
/* @keyframes shake {
    30%{
        transform: translateY(0.4vw);
    }
    55%{
        transform: rotate(5deg)
    }
    85%{
        transform: rotate(0deg)
    }
    100%{
        transform: rotate(-5deg)
    }
} */

@keyframes card-hover {
    10%{
        border-color: rgba(245, 143, 96, 0.842);
        border-width:1px;
    }
    20%{
        border-color: #ecb901;
        border-width:1.2px;
    }
    30%{
        border-color: #a2ee0a;
        border-width:1.6px;
    }
    40%{
        border-color: #10fcc5;
        border-width:1.8px;
    }
    50%{
        border-color: #0600a5;
        border-width:2px;
    }
    60%{
        border-color: #a811ff;
        border-width:2.4px;
    }
    70%{
        border-color: #db8cc2;
        border-width:2.6px;
    }
    80%{
        border-color: #fc085d;
        border-width:2.8px;
    }
    90%{
        border-color: #ff3f3f;
        border-width:2.9px;
    }
    100%{
        border-color: #a56800;
        border-width: 3px;
    }
}
.card-title {
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
    margin: 0px;
    letter-spacing: 0.08vw;
    font-size: 1.5vw;
    color: #ffffff;
    border-bottom: 1px solid#999999;
    padding-bottom: 1vw;
    cursor: default;
}

.itemprice {
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
    color: #b4b4b4;
    cursor: default;
}

.itemtype {
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    text-decoration: overline;
    color: #999999;
    cursor: default;
}

.card-body img {
    /* position:absolute;
    right: 3%;
    top:3%; */
    width: 2vw;
}

.card-body>* {
    margin: 0.7vw 0px 0.7vw 0px;
}

.ingredients {
    font-size: 1.2vw;
    color: #999999;
}

/* Button */
.btn {

    background: none;
    border-bottom: 2px solid #093A3E;
    font-size: 1vw;
    color: #131313;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: all 0.3s;
    border-radius: 12px;
    background-color: #ffffff;
    font-weight: bolder;
    box-shadow: 0 1px 0 1px #000;
    padding: 0.3vw 2vw 0.3vw 2vw;
}

.btn:before {
    content: "";
    position: absolute;
    width: 100px;
    height: 120%;
    background-color: #28ac84;
    top: 50%;
    transform: skewX(30deg) translate(-150%, -50%);
    transition: all 0.5s;
}

.btn:hover {
    background-color: #e9be7e;
    color: rgb(17, 13, 13);
    box-shadow: 0 1px 0 1px rgb(0, 0, 0);
    transform: scale(1.1);
}

.btn:hover::before {
    transform: skewX(30deg) translate(300%, -50%);
}

.btn:active {
    transform: scale(0.9);
}

/* Button red  */
.btn-red {
    background: none;
    border-bottom: 2px solid #093A3E;
    font-size: 15px;
    color: #131313;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: all 0.3s;
    border-radius: 12px;
    background-color: #fff6dc;
    font-weight: bolder;
    box-shadow: 0 1px 0 1px #000;
    padding: 0.3vw 1vw 0.3vw 1vw;
    margin: 0vw 0.2vw 0vw 0.2vw;
}

.btn-red:before {
    content: "";
    position: absolute;
    width: 100px;
    height: 120%;
    background-color: #7c1a13;
    top: 50%;
    transform: skewX(30deg) translate(-150%, -50%);
    transition: all 0.5s;
}

.btn-red:hover {
    background-color: #df1b15c9;
    color: rgb(255, 255, 255);
    box-shadow: 0 1px 0 1px rgb(0, 0, 0);
    transform: translateY(-2px);
}

.btn-red:hover::before {
    transform: skewX(30deg) translate(150%, -50%);
}

.btn-red:active {
    transform: scale(0.9);
}

div>span {
    font-weight: 600;
    font-size: larger;
    margin: 0vw 0.4vw 0vw 0.4vw;
    color: white;
}

/* Button green */
.btn-green {
    background: none;
    border-bottom: 2px solid #093A3E;
    font-size: 15px;
    color: #131313;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: all 0.3s;
    border-radius: 12px;
    background-color: #fff6dc;
    font-weight: bolder;
    box-shadow: 0 1px 0 1px #000;
    padding: 0.3vw 1vw 0.3vw 1vw;
    margin: 0vw 0.2vw 0vw 0.2vw;
}

.btn-green:before {
    content: "";
    position: absolute;
    width: 100px;
    height: 120%;
    background-color: #0c4107;
    top: 50%;
    transform: skewX(30deg) translate(-150%, -50%);
    transition: all 0.5s;
}

.btn-green:hover {
    background-color: #1c5833;
    color: rgb(255, 255, 255);
    box-shadow: 0 1px 0 1px rgb(0, 0, 0);
    transform: translateY(-2px);
}

.btn-green:hover::before {
    transform: skewX(30deg) translate(150%, -50%);
}

.btn-green:active {
    transform: scale(0.9);
}

/* input */
.input {
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
    font-size: 1vw;
    color: rgb(0, 0, 0);
    background-color: rgb(255, 255, 255);
    box-shadow: 0 0 .4vw rgba(20, 20, 20, 0.795), 0 0 0 .15vw transparent;
    border-radius: 0.7vw;
    border: none;
    outline: none;
    padding: 0.7vw;
    max-width: 190px;
    transition: .4s;
    margin: 0.6vw;
}

.input:hover {
    box-shadow: 0 0 0 0.2vw rgb(73, 55, 55);
    background: rgba(250, 247, 247, 0.555);
}

.input:focus {
    box-shadow: 0 0 0 .2vw rgb(0, 33, 46);
    background-color: rgba(255, 255, 255, 0.589);
    color: rgb(0, 33, 46);
}

.special img {
    width: 2.5vw;
    animation: sp 8s infinite alternate linear;
}

.special img:hover {
    animation-play-state: paused;
}

@keyframes sp {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

.chilly {
    animation: ch 3s infinite alternate linear;
    border-radius: 100%;
}

@keyframes ch {
    from {
        box-shadow: rgba(255, 6, 6, 0.2) 0px 54px 55px, rgba(250, 0, 0, 0.2) 0px -12px 30px, rgba(255, 0, 0, 0.2) 0px 4px 6px, rgba(255, 0, 0, 0.2) 0px 12px 13px, rgba(255, 0, 0, 0.2) 0px -3px 5px;
    }

    to {
        box-shadow: rgba(255, 6, 6, 0) 0px 54px 55px, rgba(250, 0, 0, 0) 0px -12px 30px, rgba(255, 0, 0, 0) 0px 4px 6px, rgba(255, 0, 0, 0) 0px 12px 13px, rgba(255, 0, 0, 0) 0px -3px 5px;
    }
}

.active {
    background: rgb(165, 110, 59);
    transform: scale(0.9);
    border: 2px solid white;
}

.warning {
    font-family: 'Poppins', sans-serif;
    font-weight: 900;
    margin: 0px;
    letter-spacing: 0.08vw;
    font-size: 1.5vw;
    color: #000000;
    border-bottom: 1px solid#000000;
    padding-bottom: 1vw;
    padding-top: 1vw;
    padding-left: 1vw;
    cursor: default;
    background: #ffffff44;
    width: 70%;
}
</style>