<template>

    <div id="container">
        <div id="msgs">
            <h3>{{ greeting }}</h3>
            <hr>
            <div id="today">
                <h5 style="font-weight:bolder;color:white"> Table:{{ this.myTableNumber }}</h5>
                <h5>{{ todayDate }}</h5>
                <h5>{{ timeNow }}</h5>
            </div>
        </div>

        <div v-if="order.length == 0 && !this.orderPlaced && !this.orderCancled">
            <h4>No selections made yet </h4>
        </div>
        <table v-if="order.length > 0 && !this.orderCancled" style="border-bottom:1px solid #999999">
            <th>Name</th>
            <th>Price</th>
            <th>Quantity</th>
            <th>Effective price</th>
            <tbody>
                <tr v-for="item in order" :key="item.quan">
                    <td>{{ item.name }}</td>
                    <td>{{ item.price }} ₹</td>
                    <td>X {{ item.quan }}</td>
                    <td>{{ item.ePrice }} ₹</td>
                </tr>
                <tr style="background:rgba(255, 255, 255, 0.055);">
                    <td style="border:none;font-weight: 700;">Total: {{ total }}₹</td>
                </tr>
            </tbody>
        </table>
        <div v-if="this.placeOrder && this.orderCancled == false"
            style="border-bottom:1px solid #999999;padding-bottom: 1.6rem;">
            <label for="mobile">Enter mobile number for better suggestions and user experience</label>
            <input class="input" v-model="mobile" @keypress="send_to_app" type="text" id="mobile" maxlength="10"
                placeholder="Mobile number">
        </div>
        <div v-if="this.total > 0 && this.placeOrder && this.orderCancled == false">
            <textarea cols="40" rows="4" class="input" style="resize: none;" placeholder="Any special instructions?"
                v-model="note"></textarea>
        </div>
        <hr>
        <div v-if="this.total > 0 && this.placeOrder && this.orderCancled == false">
            <button class="btn-green" @click="this.placeOrder = false, this.showOptions = true">Place Order</button>
        </div>
        <div v-if="this.showOptions && this.orderCancled == false">
            <button class="btn"
                @click="this.showOptions = false, this.paymentConfirm = true, this.type = 'online'">Online</button>
            <button class="btn" @click="this.showOptions = false, this.paymentConfirm = true, this.type = 'cash'">Cash</button>
            <button class="btn-red" @click="this.showOptions = false, this.placeOrder = true">Cancel</button>
        </div>
        <div v-if="paymentConfirm && this.orderCancled == false && order.length > 0">
            <label>Confirm ?</label>
            <button class="btn-green"
                @click="this.final = true, this.paymentConfirm = false, this.showOptions = false, this.placeOrder = false, send_order_to_DB()">Yes</button>
            <button class="btn-red" @click="this.paymentConfirm = false, this.showOptions = true">No</button>
        </div>
        <div v-if="this.final && this.type == 'online' && this.orderCancled == false" id="qr">
            <img src="../assets/qr.png" alt="Failed to load QR please reach out to admin counter">
        </div>
        <div v-if="this.final && this.type == 'cash' && this.orderCancled == false">
            <label>Reach out to admin counter for cash payment</label>
        </div>
        <div v-if="this.orderPlaced == false && this.final && this.orderCancled == false">
            <label v-if="this.cancelVisible">You can only cancel your order in 1 minute after placing order</label>
            <button class="btn-red" v-if="this.cancelVisible" @click="this.orderPlaced=false,this.orderCancled=true,cancelOrder()">Cancel Order</button>
            <label>Waiting for payment confirmation...</label>
        </div>
        <div v-if="this.orderPlaced && this.orderCancled == false">
            <label>Payment confirmed, Your order will be there soon</label>
            <button class="btn" @click="reload">Click to keep browsing</button>
            <label>Page will be reload after 20 seconds..</label>
        </div>
        <div v-if="this.orderCancled">
            <label>Order canceled</label>
            <button class="btn" @click="reload">Click to keep browsing</button>
            <label>Page will be reload after 20 seconds..</label>
        </div>
    </div>
</template>
<script>

export default {
    name: 'leftSideBar',
    props: ['order', 'total'],
    data() {
        return {
            greeting: "",
            todayDate: "",
            timeNow: "",
            placeOrder: true,
            showOptions: false,
            paymentConfirm: false,
            type: "",
            final: false,
            orderPlaced: false,
            myTableNumber:Number,
            orderCancled: false,
            note: "",
            mobile: "",
            items: [],
            user: Object,
            cancelVisible : true
        }
    },
    watch: {
        total() {
            if (this.total <= 0) {
                    this.placeOrder = true,
                    this.showOptions = false,
                    this.paymentConfirm = false,
                    this.final = false,
                    this.orderPlaced = false
            }
        }
    },
    emits: ['mobileChange']
    ,
    created() {
        let date = new Date();
        this.todayDate = date.getDate() + "/" + date.getMonth() + 1 + "/" + date.getFullYear();
        let url= window.location.href.split('!')
        this.myTableNumber = url[1];
        this.createClock();

    },
    methods: {
        cancelOrder(){
            fetch('http://localhost/orders/cancleMyOrder' + this.myTableNumber, {
                method: 'DELETE'
            });
        },
        reload() {
            location.reload();
        },
        createClock() {
            setInterval(() => {
                let date = new Date();
                this.timeNow = date.toTimeString();
                this.timeNow = this.timeNow.slice(0, 9);
                if (date.getHours() >= 12 && date.getHours() < 15) {
                    this.greeting = "Good Afternoon !"
                }
                else if (date.getHours() < 12) {
                    this.greeting = "Good Morning ! "
                }
                else if (date.getHours() >= 15) {
                    this.greeting = "Good Evening !"
                }
            }, 1000)
        },
        send_order_to_DB() {
            if (this.note.trim() == "") {
                this.note = "Regular"
            }
            fetch('http://localhost/orders/placeOrder', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    order: this.order,
                    total: this.total,
                    tableNumber: this.myTableNumber,
                    isServed: false,
                    isPaymentDone: false,
                    paymentType: this.type,
                    disableButtons: false,
                    note: this.note
                })
            });
            setTimeout(()=>{
                this.cancelVisible = false;
            },60000)
            this.checkForConfirmation();
        },
        checkForConfirmation() {
            let here = setInterval(() => {
                console.log("checking");
                fetch('http://localhost/orders/fetchMyTableOrder' + this.myTableNumber)
                    .then((response) => {
                        if (response.ok) {
                            return response.json();
                        }
                        else {
                            console.log("error");
                        }
                    }).then((data) => {
                        if (data.length == 0) {
                            this.orderCancled = true;
                            clearInterval(here);
                            setTimeout(() => {
                                location.reload();
                            }, 20000)
                        }
                        else if (data[0].isPaymentDone == true) {
                            this.orderPlaced = true;
                            if (this.mobile.length == 10) {
                                this.store_user_data();
                                clearInterval(here);
                                // setTimeout(()=>{
                                //     location.reload();
                                // },50000)
                            }
                            clearInterval(here);
                            setTimeout(() => {
                                location.reload();
                            }, 20000)
                        }
                        else {
                            //
                        }
                    })
            }, 2000)
        },
        store_user_data() {
            fetch('http://localhost/getFoodItems')
                .then((response) => {
                    if (response.ok) {
                        return response.json();
                    }
                    else {
                        console.log("error");
                    }
                }).then((data) => {
                    const results = [];
                    for (const id in data) {
                        results.push({
                            itemName: data[id].itemName,
                            ingredients: data[id].ingredients,
                        })
                    }
                    this.items = results;
                })
            setTimeout(() => {
                let sugg = [];
                this.order.forEach(item => {
                    this.items.forEach(i => {
                        if (i.itemName == item.name) {
                            i.ingredients.forEach(a => {
                                sugg.push(a);
                            });
                        }
                    });
                });
                // Fetch all users
                fetch('http://localhost/orders/findUser' + this.mobile)
                    .then((response) => {
                        if (response.ok) {
                            return response.json();
                        }
                        else {
                            console.log("error");
                        }
                    }).then((data) => {
                        if (data.length == 0) {
                            fetch('http://localhost/orders/postUserData', {
                                method: 'POST',
                                headers: {
                                    'Content-Type': 'application/json'
                                },
                                body: JSON.stringify({
                                    mobile: this.mobile,
                                    ingredients: sugg
                                })
                            });
                        }
                        else {
                            fetch('http://loclocalhostalhost/orders/updateSuggestion' + this.mobile, {
                                method: 'PATCH',
                                headers: {
                                    'Content-Type': 'application/json'
                                },
                                body: JSON.stringify({
                                    ingredients: sugg
                                })
                            });
                        }
                    })
            }, 2000);
        },
        send_to_app() {
            this.$emit('mobileChange', this.mobile);
        }
    }
}
</script>
<style scoped>
/* Popins */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

