    /* border-radius: 0.4vw; */
    border: 2px solid black;
    display: flex;
    width: 30vw;
    flex-direction: column;
    position: absolute;
    left: 69vw;
    background: rgba(0, 0, 0, 0.877);
    padding: 0.5vw;
}

#msgs {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    flex-direction: column;
    width: 30vw;
}

#today {
    width: inherit;
    padding-bottom: 1.2rem;
    display: flex;
    gap: 2rem;
    border-bottom: 1px solid #999999;
}

