    margin: 0.4vw 0.4vw 0.4vw 0.4vw;
    font-weight: 700;
    color: #999999;
    letter-spacing: 0.2vw;
    font-size: 1.2vw;
}

h4 {
    margin: 0.4vw 0.4vw 0.4vw 0.4vw;
    font-weight: 700;
    color: #999999;
}

.input {
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
    font-size: 1vw;
    color: rgb(0, 0, 0);
    background-color: rgb(255, 255, 255);
    box-shadow: 0 0 .4vw rgba(20, 20, 20, 0.795), 0 0 0 .15vw transparent;
    border-radius: 0.7vw;
    border: none;
    outline: none;
    padding: 0.7vw;
    max-width: 190px;
    transition: .4s;
    margin: 0.6vw;
}

.input:hover {
    box-shadow: 0 0 0 0.2vw rgb(73, 55, 55);
    background: rgba(250, 247, 247, 0);
}

.input:focus {
    box-shadow: 0 0 0 .2vw rgb(0, 33, 46);
    background-color: rgba(255, 255, 255, 0.589);
    color: rgb(0, 33, 46);
}

div>label {
    margin: 0.4vw 0.4vw 0.4vw 0.4vw;
    font-weight: 400;
    font-size: 1.3vw;
    word-spacing: 0.2vw;
    letter-spacing: 0.101vw;
}

.btn {

    background: none;
    border-bottom: 2px solid #093A3E;
    color: #131313;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: all 0.3s;
    border-radius: 12px;
    background-color: #ffffff;
    font-weight: bolder;
    box-shadow: 0 1px 0 1px #000;
    font-size: 1vw;
    padding: 0.5rem 1rem 0.5rem 1rem;
    margin: 0vw 0.2vw 0vw 0.2vw;
}

.btn:before {
    content: "";
    position: absolute;
    width: 100px;
    height: 120%;
    background-color: #28ac84;
    top: 50%;
    transform: skewX(30deg) translate(-150%, -50%);
    transition: all 0.5s;
}

.btn:hover {
    background-color: #e9be7e;
    color: rgb(17, 13, 13);
    box-shadow: 0 1px 0 1px rgb(0, 0, 0);
    transform: scale(1.1);
}

.btn:hover::before {
    transform: skewX(30deg) translate(150%, -50%);
}

.btn:active {
    transform: scale(0.9);
}

/* Button red  */
.btn-red {
    background: none;
    border-bottom: 2px solid #093A3E;
    color: #131313;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: all 0.3s;
    border-radius: 12px;
    background-color: #fff6dc;
    font-weight: bolder;
    box-shadow: 0 1px 0 1px #000;
    margin: 0vw 0.2vw 0vw 0.2vw;
    font-size: 1vw;
    padding: 0.5rem 1rem 0.5rem 1rem;
}

.btn-red:before {
    content: "";
    position: absolute;
    width: 100px;
    height: 120%;
    background-color: #7c1a13;
    top: 50%;
    transform: skewX(30deg) translate(-150%, -50%);
    transition: all 0.5s;
}

.btn-red:hover {
    background-color: #df1b15c9;

    color: rgb(255, 255, 255);
    box-shadow: 0 1px 0 1px rgb(0, 0, 0);
    transform: translateY(-2px);
}

.btn-red:hover::before {
    transform: skewX(30deg) translate(150%, -50%);
}

.btn-red:active {
    transform: scale(0.9);
}

div>span {
    font-weight: 600;
    font-size: larger;
    margin: 0vw 0.4vw 0vw 0.4vw;
    color: white;
}

/* Button green */
.btn-green {
    background: none;
    border-bottom: 2px solid #093A3E;
