    position: relative;
    overflow: hidden;
    transition: all 0.3s;
    border-radius: 12px;
    background-color: #fff6dc;
    font-weight: bolder;
    box-shadow: 0 1px 0 1px #000;
    margin: 0vw 0.2vw 0vw 0.2vw;
    font-size: 1vw;
    padding: 0.5rem 1rem 0.5rem 1rem;
}

.btn-green:before {
    content: "";
    position: absolute;
    width: 100px;
    height: 120%;
    background-color: #0c4107;
    top: 50%;
    transform: skewX(30deg) translate(-150%, -50%);
