}

.btn-green:active {
    transform: scale(0.9);
}

thead {
    border: 1px solid #999999;
}

th {
    background: rgba(255, 255, 255, 0.055);
    padding: 1vw 1vw 0.6vw 1vw;
    margin: 1vw;
}

tr>td {
    border-bottom: 1px solid white;
    padding: 0.7vw 0.6vw 0.6vw 0.2vw;
    text-align: center;
}

table>* {
    font-size: 1.2vw;
}

</style>